	module.exports = {"data":{"wordpressPost":{"title":"The Chic Woman&#8217;s Guide To New York By One Very Chic Woman","content":"<p>Alice Hampton has a pretty swank title.</p>\n<p>The gorgeous blonde is the Vice President Global Communications Shiseido Makeup Centre of Excellence. Impressive right? And that&#8217;s not the only impressive thing about this impossibly leggy, incredibly chic woman with the model-perfect features.</p>\n<p>Alice is also funny. She&#8217;s clever and kind. Super kind. She&#8217;s the type of friend who messages you late at night to see how you&#8217;re coping with that recent breakup, the sort that would fly thousands of miles just so you could cry on her shoulder. Not that we need her to. Well, not today.</p>\n<figure id=\"attachment_3440\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3440\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222602/JPG-Rosie--300x300.jpg\" alt=\"\" width=\"300\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222602/JPG-Rosie--300x300.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222602/JPG-Rosie--150x150.jpg 150w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222602/JPG-Rosie-.jpg 594w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@Alicehampton. Alice &#8211; with Rosie Huntington-Whiteley.</figcaption></figure>\n<p>&nbsp;</p>\n<p>Which is a good thing really as she&#8217;s New York based and crazy busy performing all the duties that go with that high-flying title. Still, when we hit her up for a quick guide to all things NY beauty she was delighted to help out. Like we said, she&#8217;d pretty ace.</p>\n<p>Here, the questions we put to her and even more importantly, the illuminating answers she shot back.</p>\n<p><strong>Best facial location in NY is &#8230;</strong></p>\n<p>The Erno Laszlo Institute in Soho. Why? Because the treatments are bliss and the results are incredible.<br />\nAsk for Judit Galambosi and request the EL Signature Facial with light therapy. The whole experience is so peaceful and relaxing I promise you will walk out with the perfect post treatment glow.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3429\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220302/JPG-Erno-Laszlo-300x214.jpg\" alt=\"\" width=\"300\" height=\"214\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220302/JPG-Erno-Laszlo-300x214.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220302/JPG-Erno-Laszlo-768x548.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220302/JPG-Erno-Laszlo.jpg 849w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>Can&#8217;t get to the salon? Grab a <a href=\"https://www.sephora.com.au/products/erno-laszlo-hydrogel-masks-set-four-masks/v/soothe-calm\">Erno Saszlo Hydrogel Mask Set, $92.</a></p>\n<p><strong>Your all time favourite local stylist is &#8230;</strong></p>\n<p>Akbar Khan from Bergdorf Goodman because he always knows what I want and what suits me best … Shopping with him is like shopping with a friend.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3430\" style=\"width: 242px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3430\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220519/JPG-Alice-NY-242x300.jpg\" alt=\"\" width=\"242\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220519/JPG-Alice-NY-242x300.jpg 242w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220519/JPG-Alice-NY.jpg 475w\" sizes=\"(max-width: 242px) 100vw, 242px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@Alicehampton</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>And the colourist who has your heart is …</strong></p>\n<p>Janelle Chapman from O&amp;M Salon in Meatpacking because, and maybe it’s because she is a fellow Aussie, she just gets it! Plus the salon is an uber cool hang out joint for ex-pats to mingle. I think every Aussie in New York goes there for a little home-away-from-home experience.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3428\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220235/JPG-OM-205x300.jpg\" alt=\"\" width=\"205\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220235/JPG-OM-205x300.jpg 205w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220235/JPG-OM.jpg 261w\" sizes=\"(max-width: 205px) 100vw, 205px\" />\n<p>Experience it yourself (sort of) with <a href=\"https://www.adorebeauty.com.au/o-m-original-mineral/o-m-seven-day-miracle-moisture-masque.html?CAWELAID=255000110000001420&amp;CAGPSPN=pla&amp;CAAGID=43088319298&amp;CATCI=pla-323117749380&amp;gclid=EAIaIQobChMIkKif2ZD_1gIVlQQqCh2BBgd7EAYYBiABEgIG5fD_BwE\">O&amp;M Seven Day Miracle Mask, $33.15.</a></p>\n<p><strong>You need your brows doing &#8211; you will definitely go to?</strong></p>\n<p>Malynda Vigliotti at the Boom Boom Brow Bar downtown because she without a doubt the best in New York.</p>\n<p><strong>Oh and lashes?</strong></p>\n<p>Again I love Malynda she is honestly your one stop brow and lash shop.</p>\n<p><strong>And manicures?</strong></p>\n<p>Paintbox. Classic or creative they leave you with the perfect shape and polish.</p>\n<p><strong>Pedicures?</strong><br />\nAs above.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3424\" style=\"width: 300px\" class=\"wp-caption alignnone\"><img class=\"size-medium wp-image-3424\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20213312/Carolyn-Murphy-Alice-300x225.jpg\" alt=\"\" width=\"300\" height=\"225\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20213312/Carolyn-Murphy-Alice-300x225.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20213312/Carolyn-Murphy-Alice-768x576.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20213312/Carolyn-Murphy-Alice-1024x768.jpg 1024w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20213312/Carolyn-Murphy-Alice.jpg 1280w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@alicehampton Yes, it&#8217;s model Carolyn Murphy and Alice &#8211; as you do &#8230;</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>You have a full day to spend with a dear friend and you’re wanting to do some serious pampering. You head straight to…</strong></p>\n<p>The Korean spa downtown because one day there feels like a year has been taken off of your life it is every New Yorkers secret place to escape and rejuvenate.</p>\n<p><strong>If you could only get one treatment a month, what would you get and where would you go?</strong></p>\n<p>I would get an acupuncture facial from Juhi Singh it is like Botox only natural. I love her and her amazing healing treatments to bits.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_3441\" style=\"width: 266px\" class=\"wp-caption alignnone\"><img class=\"wp-image-3441 size-medium\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222750/JPG-Alice-yacht-266x300.jpg\" alt=\"\" width=\"266\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222750/JPG-Alice-yacht-266x300.jpg 266w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20222750/JPG-Alice-yacht.jpg 523w\" sizes=\"(max-width: 266px) 100vw, 266px\" /><figcaption class=\"wp-caption-text\">Image: Instagram/@Alicehampton &#8211; at the Hamptons. Told you she was chic!</figcaption></figure>\n<p><strong>It’s  important for women to invest a little time and effort into their looks because …</strong></p>\n<p>If you look good, you feel good so in a city where you need to be on top of your game feeling confident and polished goes along way.</p>\n<p><strong>My secret beauty tip …</strong></p>\n<p>Corn Huskers Lotion. It softens the skin like you wouldn&#8217;t believe. I take it with me when I go skiing because no matter how harsh the weather, your skin feels so soft and moisturised all day. It is amazing.</p>\n<p>&nbsp;</p>\n<img class=\"alignnone size-medium wp-image-3427\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220057/JPG-CH-146x300.jpg\" alt=\"\" width=\"146\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220057/JPG-CH-146x300.jpg 146w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20220057/JPG-CH.jpg 251w\" sizes=\"(max-width: 146px) 100vw, 146px\" />\n<p>Try it yourself <a href=\"https://www.amazon.com/s/ref=nb_sb_noss?url=node%3D11062261&amp;field-keywords=%22corn+huskers+lotion%22&amp;rh=n%3A11062261%2Ck%3A%22corn+huskers+lotion%22\">Corn Huskers Lotion, US $12.</a></p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/10/20221701/JPG-Bellnj.jpg"},"categories":[{"slug":"beauty-talk","name":"Beauty Talk"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"7f3ffd43-bd62-5b79-abd9-b04f05a9d049"}}

